<template>
  <div>
    <div class="bar">
      <div class="container">
        POS Material List
      </div>
    </div>
    <div class="container">
      <div class="row">
        <div class="col">

          <div>
            <p>
              Thanks for building your shop with Himel, here is the material list you have selected
              </p>
            Shop Type:
            <kbd class="alert alert-secondary">{{orders.shopType}}</kbd>
            <br><br>
          </div>
          <div class="card">
            <div class="card-header">
              Material List
            </div>
            <div class="card-body">
              <div v-for="d in orders.shoppingList" style="padding: 20px 0;display: flex;border-bottom: 1px dashed rgba(162,162,162,0.29)">
                <div>
                  <img style="width: 250px;margin-right: 25px;" class="" :src="'/static/' + d['POSM List'] + '.jpg'" alt="">
                </div>
                <div>
                  <h6>{{d['POSM List']}}</h6>
                  <p>{{d.Size}}</p>
                  <p v-if="d['Raw Material']=='Download'"><a target="_blank" :href="'/static/file/' + d.Source">Download</a></p>
                  <p v-else>{{d['Raw Material']}}</p>
                  <p>Quantity: {{d.number}} nos</p>
                </div>
              </div>
            </div>
          </div>
          <div>
            <br>Your contact person:
          <br>{{country['Sales Name']}}
          <br>email: {{country['Mail Address']}}
          <br>
            <br>POS management
          <br>
            <a href="mailto:marketing@himel.com">marketing@himel.com</a>
            <br><br>
            Order Tracking ID : <kbd class="alert alert-secondary">{{orders.trackingID}}</kbd>
            <br><br>
            <button v-if="isNew" type="button" class="btn btn-danger" @click="submit">
              Submit
            </button>
            <button type="button" class="btn btn-secondary" @click="back">Back</button>
          </div>
        </div>
      </div>
    </div>
  </div>

</template>

<script>
  export default {
    data () {
      return {
        country: ''
      }
    },
    created(){
      let query = new Kinvey.Query()
      query.equalTo('Country', this.$store.state.user.data.country)
      Kinvey.DataStore.collection('country').find(query).subscribe((entities) => {
        this.country = entities[0]
      })
    },
    computed: {
      orders () {
        return this.isNew ? this.$store.state.orders : this.$route.params
      },
      isNew(){
        return !this.$route.params.trackingID
      }
    },
    methods: {
      submit(){
        if (confirm('Are you sure to submit your request?' +
            '\n\n' +
            'You can check the status of your application in "My Materials"' + '\n')) {
          let {firstName, middleName, lastName, country} = this.$store.state.user.data
          if (middleName) {
            this.orders.name = firstName + ' ' + middleName + ' ' + lastName
          } else {
            this.orders.name = firstName + ' ' + lastName
          }
          this.orders.country = country
          this.orders.status = ''
          Kinvey.DataStore.collection('orders').save(this.orders).then((entity) => {
            this.$router.push('materials')
          })
        }
      },
      back(){
        if (this.isNew) {
          this.$router.go(-1)
        } else {
          this.$router.push('materials')
        }
      }
    }
  }
</script>

<style scoped="" type="text/css" lang="scss">
  p {
    margin: 15px 0 20px 0;
  }

  .selection {
    padding: 10px 0;
  }

  kbd {
    font-family: 'Arial';
  }
</style>


