<template>
  <div>
    <div class="bar">
      <div class="container">
        Know Himel offer, easily
      </div>
    </div>
    <div class="container">
      <img src="/static/11.jpg" @click="open" alt="">
    </div>
    <b-modal ref="myModalRef" size="lg" hide-footer hide-header>
      <iframe src="http://quotefast.schneider-electric.com/EZ/?apiKey=5066fefb1e75f90f84a8682100bc40b33edffe39&lang=en_CN"></iframe>
    </b-modal>
  </div>
</template>

<script>
  export default {
    methods: {
      open(){
        this.$refs.myModalRef.show()
      }
    }
  }
</script>

<style scoped="" type="text/css" lang="scss">
  img {
    padding: 10px 0;
    width: 100%;
  }
  iframe{
    width: 100%;
    min-height: 400px;
    border: none;
  }
</style>


