<template>
  <div id="app">
    <div class="container">
      <div v-if="user!=null" class="float-right">
        <router-link to="materials"><img src="/static/16.png" alt=""> My Materials</router-link>
        <router-link to="profile"><img src="/static/17.png" alt=""> My Profile</router-link>
      </div>
      <div class="mt-2 mb-2 font-weight-bold font-italic logo">
        <router-link to="home"><img src="/static/02.jpg" alt=""></router-link>
      </div>
    </div>
    <router-view/>
  </div>
</template>

<script>
  export default {
    computed: {
      user () {
        return this.$store.state.user
      }
    }
  }
</script>

<style scoped="" type="text/css" lang="scss">
  .logo {
    img {
      width: 140px;
    }
  }

  .float-right {
    margin-top: 20px;
    a {
      color: gray;
      &:hover{
        text-decoration: none;
      }
    }
    img {
      height: 25px;
      margin-left: 15px;
    }
  }
</style>



