<template>
  <div>
    <div class="bar">
      <div class="container">
        Build Himel shop, easily
      </div>
    </div>
    <div class="container">
      <div class="row">
        <div class="col">
          <div class="download">
            <img src="/static/10.jpg" alt=""> <a href="javascript:void (0)">Download the design guidebook</a>
          </div>
          <div class="row">
            <div class="col-md-4">
              <div class="card" @click="choose('Exclusive Shop')">
                <div class="card-header">￼Exclusive shop</div>
                <div class="card-body">
                  <blockquote class="blockquote mb-0">
                    <img src="/static/05.jpg" alt="">
                    <footer class="blockquote-footer">￼Dedicated to Himel with no competitive products</footer>
                  </blockquote>
                </div>
              </div>
            </div>
            <div class="col-md-4">
              <div class="card" @click="choose('Multi-Brand Shop')">
                <div class="card-header">Multi-brand shop</div>
                <div class="card-body">
                  <blockquote class="blockquote mb-0">
                    <img src="/static/04.jpg" alt="">
                    <footer class="blockquote-footer">￼￼Multi-brand shop with big display area (10m<sup>2</sup> - 30m<sup>2</sup>)</footer>
                  </blockquote>
                </div>
              </div>
            </div>
            <div class="col-md-4">
              <div class="card" @click="choose('Himel Corner')">
                <div class="card-header">Himel corner</div>
                <div class="card-body">
                  <blockquote class="blockquote mb-0">
                    <img src="/static/06.jpg" alt="">
                    <footer class="blockquote-footer">￼Multi-brand shop with small display area (2m<sup>2</sup> - 6m<sup>2</sup>)</footer>
                  </blockquote>
                </div>
              </div>
            </div>
          </div>
          <div class="row mt-3" v-if="shopType">
            <div class="col-md-6">
              <ul class="list-group" @mouseout="hide">
                <li v-for="(l,index) in materialByShopType()" :key="l._id" class="list-group-item" @mouseover="show(l)">
                  <input type="checkbox" :id="index" :value="l" v-model="shoppingList">
                  <label :for="index">{{l['POSM List']}}</label>
                  <input class="number" type="number" v-model="l.number" min="1">
                </li>
              </ul>
              <button type="button" class="btn btn-danger mt-2" @click="confirm">Generate the material list</button>
            </div>
            <div class="col-md-6 d-flex align-items-center justify-content-center">
              <img :src="img" alt="" v-show="img" style="width: 80%">
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>

</template>

<script>
  export default {
    data () {
      return {
        material:[],
        shoppingList: [],
        shopType: null,
        img: ''
      }
    },
    created(){
      Kinvey.DataStore.collection('material').find().subscribe((entities) => {
        this.material = entities
        this.material.forEach((o) => o.number = 1)
        console.log(entities)
      })
    },
    methods: {
      choose(shopType){
        this.shopType = shopType
      },
      show(l){
        this.img = '/static/' + l['POSM List'] + '.jpg'
      },
      hide(){
        this.img = ''
      },
      confirm(){
        let {email} = this.$store.state.user
        let {name} = this.$store.state.user.data
        let now = new Date
        let trackingID = now.getFullYear() + '-' + (now.getMonth() + 1) + '-' + now.getDate()
          + '-' + Math.floor(Math.random() * 10000)
        let orders = {shopType: this.shopType, shoppingList: this.shoppingList, trackingID, email, name}
        this.$store.commit('orders', orders)
        this.$router.push('order')
      },
      materialByShopType: function () {
        return this.material.filter((o) => {
          return o['POS Category'] === this.shopType
        })
      }
    }
  }
</script>

<style scoped="" type="text/css" lang="scss">
  .card {
    cursor: pointer;
    img {
      width: 100%;
    }
  }

  .number {
    width: 45px;
    float: right;
    padding-left: 5px;
  }

  .card-header {
    text-align: center;
  }

  .blockquote-footer::before {
    content: "";
  }

  .download {
    margin: 15px 0;
    a {
      border: 1px solid gray;
      color: gray;
      padding: 8px;
      margin-left: -6px;
    }
  }

</style>


