<template>
  <div>
    <div class="bar">
      <div class="container" >
        Partner with Himel
      </div>
    </div>
    <div class="container-fluid">
      <div class="row align-items-center justify-content-center login-bg">
        <div class="col-6">
          <form>
            <div class="form-group">
              <input type="text" class="form-control" id="email"
                     v-model="user.username"
                     placeholder="Email">
            </div>
            <div class="form-group">
              <input type="password" class="form-control" id="password" placeholder="Password"
                     v-model="user.password">
            </div>
            <button type="button" @click="login" class="btn btn-danger">Login</button>
            <div class="form-group re-bg">
                <router-link to="register">Create an account</router-link>
                <br>
                <router-link to="#">Forgot password or trouble logging in?</router-link>
            </div>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        user: {
          username: '',
          password: ''
        }
      }
    },
    created(){
      // TODO: must logout first if an active user already exists
      Kinvey.User.logout()
    },
    methods: {
      login(){
        Kinvey.User.login(this.user)
          .then((user) => {
            this.$store.commit('user', user);
            this.$router.push('home')
          })
          .catch((error) => {
            console.log(error)
            alert('login failed!')
          });
      }
    }
  }
</script>

<style type="text/css" lang="scss">
  .login-bg {
    padding: 80px 0;
    input, select {
      background: #f6f5f6;
    }
  }

  .re-bg{
    margin-top: 10px;
    background: #f9f9fb;
    padding: 10px;
    clear: both;
    a {
      color: #9da3b7 !important;
      line-height: 1.5;
      font-size: 14px;
    }
  }
</style>


