<template>
  <div>
    <div class="bar">
      <div class="container" >
        Partner with Himel
      </div>
    </div>
    <div class="container login-bg">
      <div class="row align-items-center justify-content-center">
        <div class="col-6">
          <form>
            <div class="form-group">
              <label for="email">Email <span class="text-danger">*</span></label>
              <input type="email" class="form-control" id="email" v-model="user.email"
                     placeholder="Email">
            </div>
            <div class="form-group">
              <label for="salu">Salu. <span class="text-danger">*</span></label>
              <select class="form-control" id="salu" v-model="user.salu">
                <option></option>
                <option value="Mr">Mr</option>
                <option value="Ms">Ms</option>
              </select>
            </div>
            <div class="form-group">
              <label for="firstName">First Name <span class="text-danger">*</span></label>
              <input type="text" class="form-control" id="firstName" v-model="user.firstName"
                     placeholder="First name">
            </div>
            <div class="form-group">
              <label for="middleName">Middle Name </label>
              <input type="text" class="form-control" id="middleName" v-model="user.middleName"
                     placeholder="Middle name">
            </div>
            <div class="form-group">
              <label for="lastName">Last Name <span class="text-danger">*</span></label>
              <input type="text" class="form-control" id="lastName" v-model="user.lastName"
                     placeholder="Last name">
            </div>
            <div class="form-group">
              <label for="password">Password <span class="text-danger">*</span></label>
              <input type="password" class="form-control" id="password" v-model="user.password"
                     placeholder="Password">
            </div>
            <div class="form-group">
              <label for="password2">Retype your Password <span class="text-danger">*</span></label>
              <input type="password" class="form-control" id="password2" v-model="user.password2"
                     placeholder="Retype your Password">
            </div>
            <div class="form-group">
              <label for="company">Company <span class="text-danger">*</span></label>
              <input type="text" class="form-control" id="company" v-model="user.company"
                     placeholder="Company">
            </div>
            <div class="form-group">
              <label for="parter">Parter Type <span class="text-danger">*</span></label>
              <select class="form-control" id="parter" v-model="user.parterType">
                <option></option>
                <option value="Internal Employee">Internal Employee</option>
                <option value="T1 Distributor">T1 Distributor</option>
                <option value="Exclusive POS">Exclusive POS</option>
                <option value="General POS">General POS</option>
                <option value="Panel Builder">Panel Builder</option>
                <option value="OEM">OEM</option>
              </select>
            </div>
            <div class="form-group">
              <label for="country">Country <span class="text-danger">*</span></label>
              <select class="form-control" v-model="user.country" id="country">
                <option v-for="c in countries" v-bind:value="c">
                  {{c}}
                </option>
              </select>
            </div>
            <div class="form-group">
              <label for="city">City <span class="text-danger">*</span></label>
              <input type="text" class="form-control" id="city" v-model="user.city"
                     placeholder="City">
            </div>
            <div class="form-group">
              <label for="address">Address <span class="text-danger">*</span></label>
              <input type="text" class="form-control" id="address" v-model="user.address"
                     placeholder="Address">
            </div>
            <div class="form-group">
              <label for="telephone">Telephone <span class="text-danger">*</span></label>
              <input type="text" class="form-control" id="telephone" v-model="user.telephone"
                     placeholder="+Country code + telephone">
            </div>
            <div class="form-group">
              <label for="how-to-know">How do you know Himel Partner Portal<span class="text-danger">*</span></label>
              <select class="form-control" id="how-to-know" v-model="user.howToKnow">
                <option></option>
                <option value="Himel staff">Himel staff</option>
                <option value="Himel Website">Himel Website</option>
                <option value="Himel POS">Himel POS</option>
                <option value="Search engine">Search engine</option>
                <option value="Advertisement">Advertisement</option>
                <option value="Social Media">Social Media</option>
                <option value="Event & Exhibition">Event & Exhibition</option>
                <option value="Others">Others</option>
              </select>
            </div>
            <div class="text-danger err-prompt" v-if="formStatu == 1">
              All the fields are required!
            </div>
            <div class="text-danger err-prompt" v-if="formStatu == 2">
              The second password doesn't equal the first password!
            </div>
            <button type="button" class="btn btn-danger mr-2" @click="signup">Register</button>
            <router-link to="login">Return to login</router-link>
          </form>
        </div>
      </div>
    </div>
  </div>

</template>

<script>
  export default {
    data () {
      return {
        user: {
          email: '',
          salu:'',
          firstName: '',
          middleName: '',
          lastName: '',
          password: '',
          password2: '',
          company: '',
          parterType: '',
          country: '',
          city: '',
          address: '',
          telephone: '',
          howToKnow: '',
        },
        formStatu: 0
      }
    },
    created(){
      // TODO: must logout first if an active user already exists
      Kinvey.User.logout()
    },
    computed: {
      countries () {
        return countries
      }
    },
    methods: {
      signup(){
        for (let p in this.user) {
          if (p != 'middleName' && !this.user[p]) {
            this.formStatu = 1 // some of the fields are empty
          }
        }
        let {password, password2} = this.user
        if (password != password2) {
          this.formStatu = 2 // the 2 passwords are not equal
        }
        if (this.formStatu != 0) {
          setTimeout(() => {
            this.formStatu = 0
          }, 3000)
          return
        }

        let temp = JSON.parse(JSON.stringify(this.user))
        temp.username = temp.email
        delete temp.password2

        // https://devcenter.kinvey.com/html5/guides/users#signup
        let user = new Kinvey.User();
        user.signup(temp)
          .then((user) => {
            this.$router.push('thanks')
          })
          .catch((error) => {
            console.log(error)
          });
      }
    }
  }
</script>

<style type="text/css" lang="scss">
  .login-bg{
    a {
      color: #b94235;
    }
  }
  .err-prompt{
    padding-bottom: 15px;
  }
</style>


