<template>
  <div>
    <div class="bar">
      <div class="container">
        POS Material List
      </div>
    </div>
    <div class="container">
      <div class="row">
        <div class="col">
          <b-tabs>
            <b-tab title="Ongoing orders" active>
              <table class="table">
                <tr v-for="o in orders">
                  <td>{{o.shopType}} {{o.trackingID}}</td>
                  <td>
                    <router-link class="btn btn-danger" :to="{ name: 'order', params: o}">Details</router-link>
                  </td>
                </tr>
              </table>
            </b-tab>
            <b-tab title="Finished orders">
              None
            </b-tab>
          </b-tabs>

        </div>
      </div>
    </div>
  </div>

</template>

<script>
  export default {
    data () {
      return {
        orders: []
      }
    },
    created(){
      let query = new Kinvey.Query()
      query.equalTo('email', this.$store.state.user.email)
      Kinvey.DataStore.collection('orders').find(query).subscribe((entities) => { // why this callback is executed twice?
        this.orders = entities
      })
    },
    methods: {
      submit(){
      }
    }
  }
</script>

<style scoped="" type="text/css" lang="scss">
  .tabs {
    padding-top: 15px;
  }

  .table td {
    border-top: none;
    padding-bottom: 0;
  }

  .tab-pane {
    padding-top: 10px;
  }
</style>


